<template>
  <div>
    <h1>Welcome</h1>
    <div class="outer_label">
      <img src="../assets/login_icon.png">
    </div>
    <div class="login_form">

      <el-input class="user" maxlength=11 prefix-icon="el-icon-user" placeholder="手机号" v-model="userName"/>
      <el-input class="password"  maxlength=16 show-password prefix-icon="el-icon-lock" placeholder="密码" v-model="password"/>
      <el-button class="login_btn" @click.native="login" type="primary" round :loading="isBtnLoading">登录</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  props: {
    msg: String
  },
  data() {
    return {
      userName: '',
      password: ''
    }
  },

  methods: {
    login() {
      if (!this.userName) {
          this.$message.error('请输入用户名');
          return;
      }
      if (!this.password) {
          this.$message.error('请输入密码');
          return;
      }
    }
  }
}
</script>

